<template>
  <div class="return-visit-form">
    <div class="main">
      <ol>
        <li class="row" v-for="question of data[$route.query.method]" :key="question.id">
          <div class="content">{{ question.q }}</div>
          <div class="selection">
            <van-radio-group v-model="question.a" direction="horizontal">
              <van-radio :name="1">是</van-radio>
              <van-radio :name="0">否</van-radio>
            </van-radio-group>
          </div>
        </li>
      </ol>
      <div class="submit-btn" @click="submitBtn">提交</div>
    </div>
  </div>
</template>

<script>
import {shhdxxQuerydata4objecthf, shhdxxQuerydata4objectxq, yscgjQuerydata4objectxq, yscgjQuerydata4objecthf} from '@/assets/js/api';
export default {
  data() {
    return {
      data: {
        questions: [{
          id: 1,
          q: '是否严格遵守国家法律法规、政治纪律规矩，严密保守所知悉的国家秘密；',
          a: null
        }, {
          id: 2,
          q: '是否有违反所前往国（境）外法律的行为；',
          a: null
        }, {
          id: 3,
          q: '是否出入过国（境）外违法复杂场所；',
          a: null
        }, {
          id: 4,
          q: '是否被境外间谍情报机关或可疑人员约谈、攀拉、纠缠；',
          a: null
        }, {
          id: 5,
          q: '是否受胁迫或诱骗参与了间谍组织、敌对组织或者其他危害我国家安全的组织、机构，实施了危害国家安全的行为；',
          a: null
        }, {
          id: 6,
          q: '是否发表了违背国家政策的言论；',
          a: null
        }, {
          id: 7,
          q: '是否有损害国家利益和形象的行为；',
          a: null
        }, {
          id: 8,
          q: '是否被境外海关、边检或执法部门进行盘查或非正常检查；',
          a: null
        }, {
          id: 9,
          q: '是否被民运、法轮功、“疆独”、“藏独”等敌对势力滋扰；',
          a: null
        }, {
          id: 10,
          q: '是否遇有恐怖活动、政治突发事件、社会突发事件等；',
          a: null
        }, {
          id: 11,
          q: '是否未经请示接受境外媒体采访；',
          a: null
        }, {
          id: 12,
          q: '是否接受境外机构、人员的资助；',
          a: null
        }, {
          id: 13,
          q: '是否遇到人身财产安全威胁；',
          a: null
        }, {
          id: 14,
          q: '是否遇有其它可疑情况；',
          a: null
        }],
        activity: [{
          id: 1,
          q: '有无违反“两个维护”、“四个意识”、三个表率”的行为；',
          a: null
        }, {
          id: 2,
          q: '有无违反政治纪律和政治规矩的行为；',
          a: null
        }, {
          id: 3,
          q: '有无违反中央八项规定和实施细则精神的行为；',
          a: null
        }, {
          id: 4,
          q: '有无违反“四不”(不乱说、不吃请、不收受、不请托)要求的行为；',
          a: null
        }, {
          id: 5,
          q: '有无利用名贵特产类特殊资源谋取私利的行为；',
          a: null
        }, {
          id: 6,
          q: '有无其他违反党纪的行为；',
          a: null
        }]
      },
      dspToken: localStorage.dspToken
    };
  },
  mounted() {
    if (this.$route.query.method === 'activity') {
      shhdxxQuerydata4objectxq({
        hdxxId: this.$route.query.id,
        dspToken: this.dspToken
      }).then(res => {
        console.log(res);
      });
    } else if (this.$route.query.method === 'questions') {
      yscgjQuerydata4objectxq({
        hfbxxId: this.$route.query.id,
        dspToken: this.dspToken
      }).then(res => {
        console.log(res);
      });
    }
  },
  methods: {
    submitBtn() {
      let data = {};
      this.data[this.$route.query.method].forEach(function(o, index) { data['mx' + index] = o.a; });
      if (this.$route.query.method === 'activity') {
        shhdxxQuerydata4objecthf({
          ...data,
          dspToken: this.dspToken
        }).then(res => {
          console.log(res);
        });
      } else if (this.$route.query.method === 'questions') {
        yscgjQuerydata4objecthf({
          ...data,
          dspToken: this.dspToken
        }).then(res => {
          console.log(res);
        });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.return-visit-form {
  padding: 24px;
  background: #f5f5f5;
  .main {
    background: #fff;
    padding: 30px 40px;
    font-size: 34px;
    font-weight: normal;
    font-stretch: normal;
    letter-spacing: 0px;
    color: #353535;
    .row {
      &:not(:last-child) {
        margin-bottom: 50px;
      }
      /deep/ .van-radio-group {
        line-height: 1.5;
        .van-radio {
          padding-right: 120px;
          .van-radio__icon {
            font-size: inherit;
          }
          .van-radio__label {
            height: auto;
            line-height: inherit;
          }
        }
      }
      .content {
        padding-bottom: 10px;
      }
    }
    .submit-btn {
      height: 90px;
      background-color: #3198ed;
      box-shadow: 0px 5px 5px 0px rgba(89, 166, 250, 0.1);
      border-radius: 45px;
      line-height: 90px;
      color: #ffffff;
      font-size: 36px;
      text-align: center;
      margin: 40px 0 0;
      letter-spacing: 7px;
    }
  }
}
</style>



